import React, { Component } from 'react'
import Grid from '@hi-ui/hiui/es/grid'
import Input from '@hi-ui/hiui/es/input'
import Button from '@hi-ui/hiui/es/button'
import Icon from '@hi-ui/hiui/es/icon'
import Badge from '@hi-ui/hiui/es/badge'
import Popover from '@hi-ui/hiui/es/popover'
import Modal from '@hi-ui/hiui/es/modal'
import './index.scss'

const { Row, Col } = Grid

class HomePortal extends Component {
  constructor (props) {
    super(props)
    this.state = {
      showShortcutModal: false,
      showBusinessModal: false
    }
  }

  renderShortcut () {
    let shortcutList = []
    for (let i = 0; i < 3; i++) {
      shortcutList.push(
        <Col key={i} span={8}>
          <div className='shortcut'>
            <h4>AppTitle</h4>
            <p>Application discription inscribed userScene and usage</p>
            <Button
              size='small'
              onClick={() => { this.setState({ showShortcutModal: true }) }}
            >
              use <Icon name='right' />
            </Button>
          </div>
        </Col>
      )
    }
    return shortcutList
  }

  renderBusiness () {
    let businessList = []
    for (let i = 0; i < 6; i++) {
      businessList.push(
        <div className='business--item' key={i}>
          <Row justify='space-between'>
            <Col>
              <span className={(i % 2) ? 'business--item__title success' : ' business--item__title error'}></span>
            </Col>
            <Col>
              <span className='business--item__time'>2018-10-21</span>
            </Col>
          </Row>
          <Row justify='space-between'>
            <Col>
              <div className='business--item__detail'></div>
            </Col>
            <Col>
              <Button
                type='line'
                size='small'
                onClick={() => { this.setState({ showBusinessModal: true }) }}
              >
                
              </Button>
            </Col>
          </Row>
        </div>
      )
    }
    return businessList
  }

  render () {
    return (
      <div className='page page--portal'>
        <div className='portal--container'>
          <Row justify='space-between'>
            <Col>
              <Input
                style={{ width: '250px' }}
                append={<Button type='primary'><Icon name='search' /></Button>}
                placeholder='Search'
              />
            </Col>
            <Col>
              <Badge dot>
                <Icon name='prompt' style={{ fontSize: '20px' }} />
              </Badge>
              <Popover content={'99'} placement='bottom' trigger='hover' style={{ margin: '10px 10px' }}>
                <span> </span>
              </Popover>
            </Col>
          </Row>
          <Row>
            <Col span={24}>
              <div className='portal--item__header'>
                <Icon name='menu' style={{ fontSize: '28px', marginRight: '12px' }} />
                <span></span>
              </div>
              <Row gutter justify='space-between'>
                {this.renderShortcut()}
              </Row>
            </Col>
          </Row>
          <Row>
            <Col span={24}>
              <div className='portal--item__header'>
                <Icon name='time' style={{ fontSize: '28px', marginRight: '12px' }} />
                <span className='title'></span>
              </div>
              <Row>
                <Col span={24}>
                  <div className='business'>{this.renderBusiness()}</div>
                </Col>
              </Row>
            </Col>
          </Row>
          <Modal
            size='small'
            title='AppTitle'
            show={this.state.showShortcutModal}
            backDrop
            onConfirm={() => { this.setState({ showShortcutModal: false }) }}
            onCancel={() => { this.setState({ showShortcutModal: false }) }}
          >
            <p>Application discription inscribed userScene and usage</p>
          </Modal>
          <Modal
            size='small'
            title=''
            show={this.state.showBusinessModal}
            backDrop
            onConfirm={() => { this.setState({ showBusinessModal: false }) }}
            onCancel={() => { this.setState({ showBusinessModal: false }) }}
          >
            <p></p>
          </Modal>
        </div>
      </div>
    )
  }
}

export default HomePortal
